import { Avatar } from '@material-ui/core'
import React from 'react'
import VerifiedIcon from '@material-ui/icons/CheckCircleOutlineOutlined'

function ChannelRow({image, channelTitle, verified, channelId, description}) {
    return (
        <div className="channelRow">
            <div className="channelRow_logo_parent">
            <Avatar className ="channelRow_logo"
            alt ={channelTitle} src={image}/>
            </div>
            <div className="channelRow_text">
                <h4><a href={`https://www.youtube.com/channel/${channelId}`
            }>{channelTitle}</a> {verified && <VerifiedIcon/>}</h4>
                <p>1M subscribers . 100 videos</p>
                <p className="description">{description}</p>
            </div>
            <div className="subscribe_parent">
            <button className="subscribe">Subscribe</button>
            </div>
        </div>
    )
}

export default ChannelRow
