
import React from 'react'
import './VideoCard.css'


function VideoCard({videoTitle,channelTitle,thumbnail,channelId,duration,views,publishedAt}) {

    const getTimeStamp =()=>{
        
        // formatting today's date 
        let today = new Date();
        const dd = String(today.getDate()).padStart(2, '0');
        const mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        const yyyy = today.getFullYear();
        today = mm + '/' + dd + '/' + yyyy;

        // formatting published date
        let pDate = publishedAt.slice(0,11);
        pDate = pDate.slice(5,7)+"/"+pDate.slice(8,10)+"/"+pDate.slice(0,4);

        const date1 = new Date(today);
        const date2 = new Date(pDate);
        const diffTime = Math.abs(date2 - date1);
        const diffDays = Math.round(diffTime / (1000 * 60 * 60 * 24)); 
        const diffHours = Math.round(diffTime / (1000 * 60 * 60));
        if(diffDays<1) return diffHours+" hours ago";
        else if(diffDays===1) return diffDays+" day ago";
        return diffDays +" days ago";   
    }

    const getDuration=()=>{
        let d = duration;
        d = d.slice(2);
        let mins="";
        let i=0;
        for(let x=0;x<d.length;x++){
        if(d.charAt(x)==="M"){
            break;
        }
        mins+=d.charAt(x)
        i=x+2;

    }
   
        const secs = d.slice(i,d.length-1);
        return mins+":"+secs;
    }

    const getViews=()=>{
        let v = parseInt(views);
        if(v<1000000) return (v/1000).toFixed(1)+"k views";
        else if(v>=1000000 && v<1000000000) return ((v/1000)/1000).toFixed(1)+"M views";
        else if(v>=1000000000) return (v/1000000000).toFixed(1)+"B views";
    }

    return (
        <div className="videoCard">
            <div className="durationOnImg">
            <img className={"videoCard_thumbnail"} src={thumbnail} alt={videoTitle}/>
            <p className="duration">{getDuration()}</p>
            </div>
            <div className="videoCard_info">
                <div className="videoCard_text">
                    <h4>{videoTitle}</h4>
                    <a href={channelId}>{channelTitle}</a>
                    <p>{getViews()} {getTimeStamp()}</p>
                </div>
            </div>
        </div>
    )
}

export default VideoCard
