import React from 'react'

function VideoRow({image,channelId, channelTitle, videoId, videoTitle,description}) {
    return (
        <div className="videoRow">
            <img src={image} alt={channelTitle}/>
            <div className="videoRow_text">
                <h3><a href={`https://www.youtube.com/video/${videoId}`}>{videoTitle}</a></h3>
              
                1M views | 1 day ago
                    <a href={`https://www.youtube.com/channel/${channelId}`}>{channelTitle}</a>
                <div className="description">{description}</div>
            </div>
            <div className="empty">
           
            </div>
        </div>
    )
}

export default VideoRow
