import { TuneOutlined } from '@material-ui/icons'
import React from 'react'
import './SearchPage.css'
import VideoRow from './VideoRow'
import ChannelRow from './ChannelRow'

function SearchPage({data}) { 

    return (
        <div className="searchPage">
            <div className="searchPage_filter">
                <TuneOutlined/>
                <h2>FILTER</h2>
            </div>

            <hr/>
            {data.map((obj,index)=>{
                if(obj.id.kind==="youtube#channel"){
                    return <ChannelRow key={index}
                    image={obj.snippet.thumbnails.medium.url}
                    channelTitle={obj.snippet.channelTitle}
                    verified
                    description={obj.snippet.description}
                    channelId={obj.snippet.channelId}/>
                }
                else{
                   return <VideoRow key={index}
                channelTitle={obj.snippet.channelTitle}
                channelId = {obj.snippet.channelId}
                videoTitle={obj.snippet.title}
                videoId={obj.id.videoId}
                image={obj.snippet.thumbnails.medium.url}
                description={obj.snippet.description}/>
                }
            })
        }
        </div>
    )
}

export default SearchPage
